import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from '../auth/auth.guard';

import {SiteEngineerComponent} from './site-engineer/site-engineer.component';
import {SiteEngineerWorkOrderDetailsComponent} from './site-engineer-work-order-details/site-engineer-work-order-details.component'
import {SiteEngineerWorkOrdersListComponent} from './site-engineer-work-orders-list/site-engineer-work-orders-list.component';
import {CreateNewOrderComponent} from './create-new-order/create-new-order.component';
import {CreateNewDetailComponent} from './create-new-detail/create-new-detail.component'

const adminRoutes: Routes = [
    {
        path: '',
        component: SiteEngineerComponent,
        canActivate: [AuthGuard],
        children: [
          {
            path: '',
            canActivateChild: [AuthGuard],
            children: [
              { path: 'orderlist', component: SiteEngineerWorkOrdersListComponent},
              { path: 'OrderDetails/:id', component: SiteEngineerWorkOrderDetailsComponent},
              {path: 'newOrder', component: CreateNewOrderComponent},
              {path: 'newDetail/:id', component: CreateNewDetailComponent}
            ]
          }
        ]
      }
];

@NgModule({
    imports: [
      RouterModule.forChild(adminRoutes)
    ],
    exports: [
      RouterModule
    ]
  })
  export class SiteEngineerRoutingModule {}