import { Component, OnInit } from '@angular/core';
import {WorkOrder} from 'src/app/WorkOrder';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {Detail} from 'src/app/Detail';
import {WorkOrderService} from 'src/app/work-order.service';

@Component({
  selector: 'app-site-engineer-work-order-details',
  templateUrl: './site-engineer-work-order-details.component.html',
  styleUrls: ['./site-engineer-work-order-details.component.css']
})
export class SiteEngineerWorkOrderDetailsComponent implements OnInit {

  Order?: WorkOrder;
  Details ?: Detail[];

  progress?:number;
  id?:number;
  creationdate?:string;
  startdate?:string;
  enddate?:string;
  Operationdescription?:string;
  

  constructor(private route: ActivatedRoute, private WorkOrderService:WorkOrderService,private location: Location) { }

  ngOnInit(): void {
    this.getOrder();
    this.Details = this.Order?.Details;
    this.Details = this.Order?.Details;
    this.getprogress();
    this.id =  this.Order?.Id;
    this.creationdate =  this.Order?.creationDate;
    this.startdate =  this.Order?.StartDate;
    this.enddate =  this.Order?.EndDate;
    this.Operationdescription =  this.Order?.OperationDescription;
  }
  
  getOrder(): void{
    let nb;
    if((nb = this.route.snapshot.paramMap.get('id')) != null){
     const id = +nb;
     this.WorkOrderService.getOrder(id).subscribe(order => this.Order = order);
    }
  }

  

  goBack(): void{
    this.location.back();
  }

  getprogress() :void{
    
    var progress = 0;
    var count = 0;
    this.Details?.forEach((item) =>{
      
      progress += item.progress.valueOf();
      count ++;
    });
    this.progress =  progress/count;
  }

}
