import { Component, OnInit } from '@angular/core';
import {WorkOrder} from "src/app/WorkOrder";
import {WorkOrderService} from "src/app/work-order.service";


@Component({
  selector: 'app-site-engineer-work-orders-list',
  templateUrl: './site-engineer-work-orders-list.component.html',
  styleUrls: ['./site-engineer-work-orders-list.component.css']
})
export class SiteEngineerWorkOrdersListComponent implements OnInit {

  orders?: WorkOrder[];

  constructor(private workOrderService:WorkOrderService) { }

  ngOnInit(): void {
    this.getOrders();
  }

  getOrders():void{
    this.workOrderService.getOrders().subscribe(WorkOrder => this.orders = WorkOrder);
  }
}
